'use strict';
var React = require('react');
var ReactDOM = require('react-dom');
var projectLoadHelper = require('../../helper/projectLoadHelper');
var appTypes = require('../../types/appTypes.js');
var util = require('../../helper/util');

/**
 @Author : mengxiaojiang
 @CreateDate :
 @Descriptor : 图片轮播组件中的各个图片层
 */
var ImageBackground = React.createClass({

  render: function () {
    var c = this.props.color;
    if(util.Color.toRGBA(c).a === 0){
      c = '#fff'
    }
    return (<div key="_" className="imgBackground" style={{backgroundColor : c}}>
      <img
        ref="img"
        id={this.props.controlID + '-image'}
        src={projectLoadHelper.loadImage(this.props.URL)}
        onLoad={this.onAdjustImgBoundsWithLoad}
      />
    </div>);
  },

  /**
   * 图片加载完成后，根据布局模式调整图片的区域
   */
  onAdjustImgBoundsWithLoad: function () {
    var fitMode = this.props.fitMode;
    var img = ReactDOM.findDOMNode(this.refs.img);
    var dom = ReactDOM.findDOMNode(this);
    var imgW;
    var imgH;
    var w = dom.clientWidth;
    var h = dom.clientHeight;
    var sx = 1;
    var sy = 1;
    var sw;
    var sh;
    var left;
    var top;
    if (img) {
      imgW = img.clientWidth;
      imgH = img.clientHeight;

      if (imgW !== 0) {
        sx = w / imgW;
      }
      if (imgH !== 0) {
        sy = h / imgH
      }
      if (fitMode === appTypes.ImageCarouselFitMode.AUTO) {
        sx = Math.max(sx, sy);
        sy = Math.max(sx, sy);
      }
      sw = imgW * sx;
      sh = imgH * sy;
      left = (w - sw) / 2;
      top = (h - sh) / 2;
      $(img).css({
        width: sw,
        height: sh,
        left: left,
        top: top
      });
    }
  }
});

module.exports = ImageBackground;



