'use strict';
var React = require('react');
var _ = require('underscore');
var styleHelper = require('../../helper/styleHelper');
/*
 @Author : mengxiaojiang
 @CreateDate : 2016-4-1
 @Descript : 内容面板组件
 */
var ContentPanel = React.createClass({
  getInitialState: function () {
    return {
      selectedIndex: parseInt(this.props.selectedIndex, 10)
    };
  },
  render: function () {
    var items = this.props.items;
    var w = styleHelper.getWidth(this.props);
    var h = styleHelper.getHeight(this.props);
    var style = {
      clip : 'rect(1 1 '+ (w - 1) + ' '+ (h - 1) + ')'
    };
    styleHelper.StyleUtil(this.props).background().base().bounds().border().default(style);
    return (
      <div
        className={"component component-" + this.props.controlTypeID}
        key={this.props.controlID}
        id={this.props.controlID}
        style={style}>
        {
          this.props.children
        }
      </div>
    )
  },

  componentDidMount : function(){
    var index = !_.isUndefined(this.props.currentIndex) ? this.props.currentIndex : this.props.selectedIndex;
    var i;
    for(i = 0;i<this.props.items.length;i++){
      if(i === index){
        $('#' + this.props.items[i].controlID).css({'z-index':1,display:'block'})
      }else{
        $('#' + this.props.items[i].controlID).css({'z-index':0,display:'none'})
      }
    }
  }
});


module.exports = ContentPanel;



