'use strict';
var React = require('react');
var ReactDOM = require('react-dom');
var ScrollBox = require('osx-scrollview');
var Border = require('../common/LineModeBorder');
var styleHelper = require('../../helper/styleHelper');
/*
 @Author : mengxiaojiang
 @CreateDate : 2016-3-31
 @Descript : 滚动区组件
 */
var ScrollView = React.createClass({
  render: function () {
    var content = this.props.items[0];
    return (
      <div style={{
        width : '100%',
        height : '100%',
        borderWidth : '1px',
        borderColor : styleHelper.getBorderColor(this.props),
        backgroundColor : styleHelper.getBackgroundColor(this.props),
        overflow:'hidden',
        zIndex : this.props.zOrder
      }}
           onMouseDown={this.onMouseDown}
           onMouseUp={this.onMouseUp}
           onMouseMove={this.onMouseMove}
           onScroll={this.onScroll}
      >
        <ScrollBox
          id={this.props.controlID + '-scrollbox'}
          scrollTop={this.props.scrollTop || 0}
          disableHScroll={!this.props.canHoriScroll}
          disableVScroll={!this.props.canVertScroll}>
          <div ref="viewPort" style={{
            width : content.w + 20,
            height : content.h + 20,
            overflow : 'hidden'
          }}>
          {this.props.children}
          </div>
        </ScrollBox>
        {
          this.props.borderVisible &&
            <Border {...{borderVisible:true}}/>
        }
      </div>
    );
  },

  onScroll : function(e){
    e.stopPropagation();
  },

  onMouseDown : function(e){
    e.stopPropagation();
    this.mousePress = true;
    this.downPoint = {
      x : e.nativeEvent.layerX,
      y : e.nativeEvent.layerY
    }
  },

  onMouseUp : function(e){
    e.stopPropagation();
    this.mousePress = false;
  },

  onMouseMove : function(e){
    var offsetX;
    var offsetY;
    e.stopPropagation();
    if(this.mousePress){
      offsetX = e.nativeEvent.layerX - this.downPoint.x;
      offsetY = e.nativeEvent.layerY - this.downPoint.y;
      this.doScrollBy(offsetX,offsetY);
      this.downPoint = {
        x : e.nativeEvent.layerX,
        y : e.nativeEvent.layerY
      }
    }
  },

  doScrollBy : function(x,y){
    var content = ReactDOM.findDOMNode(this.refs.viewPort);

    var top = content.offsetTop + y;
    var left = content.offsetLeft + x;
    $(content).css({left : left, top : top});

  }
});

module.exports = ScrollView;



