'use strict';

var React = require('react');
var GlobalConst = require('../../types/GlobalConst');
var styleHelper = require('../../helper/styleHelper');

/**
 * 柱状图
 */
var ColumnChart = React.createClass({

  render: function () {
    var w = styleHelper.getWidth(this.props);
    var h = styleHelper.getHeight(this.props);
    var backgroundStyle = {
      width: '100%',
      height: '100%',
      borderWidth: 2,
      borderStyle: 'solid',
      borderColor: GlobalConst.GLOBAL_COLOR.DEFAULT_BORDER_COLOR,
      borderRightStyle: 'none',
      borderTopStyle: 'none'
    };

    var chartWidth = Math.round(w / 7);

    var offset = chartWidth;

    var chart1Style = {
      position: 'absolute',
      left: offset,
      bottom: 0,
      width: chartWidth,
      height: Math.round(h / 2),
      border: '1px solid ' + GlobalConst.GLOBAL_COLOR.DEFAULT_BORDER_COLOR,
      borderBottomStyle: 'none',
      backgroundColor: GlobalConst.GLOBAL_COLOR.LIGHT_GRAY_COLOR,
      boxSizing: 'border-box'
    };

    offset = chartWidth * 2 + 6;

    var chart2Style = {
      position: 'absolute',
      left: offset,
      bottom: 0,
      width: chartWidth,
      height: Math.round(h * 5 / 8),
      border: '1px solid ' + GlobalConst.GLOBAL_COLOR.DEFAULT_BORDER_COLOR,
      borderBottomStyle: 'none',
      backgroundColor: GlobalConst.GLOBAL_COLOR.SYSTEM_GRAY,
      boxSizing: 'border-box'
    };

    offset = chartWidth * 4;

    var chart3Style = {
      position: 'absolute',
      left: offset,
      bottom: 0,
      width: chartWidth,
      height: Math.round(h * 7 / 8),
      border: '1px solid ' + GlobalConst.GLOBAL_COLOR.DEFAULT_BORDER_COLOR,
      borderBottomStyle: 'none',
      backgroundColor: GlobalConst.GLOBAL_COLOR.LIGHT_GRAY_COLOR,
      boxSizing: 'border-box'
    };

    offset = chartWidth * 5 + 6;
    var chart4Style = {
      position: 'absolute',
      left: offset,
      bottom: 0,
      width: chartWidth,
      height: Math.round(h * 0.75),
      border: '1px solid ' + GlobalConst.GLOBAL_COLOR.DEFAULT_BORDER_COLOR,
      borderBottomStyle: 'none',
      backgroundColor: GlobalConst.GLOBAL_COLOR.SYSTEM_GRAY,
      boxSizing: 'border-box'
    };

    return (<div style={backgroundStyle}>
      <div style={chart1Style}/>
      <div style={chart2Style}/>
      <div style={chart3Style}/>
      <div style={chart4Style}/>
    </div>);
  }


});

module.exports = ColumnChart;



