'use strict';

var React = require('react');
var VerticalScrollBar = require('../common/VerticalScrollBar');
var TextParts = require('../common/TextParts');
var Border = require('../common/Border');

var TextArea = React.createClass({

  render: function () {

    return (<Border {...this.props}
        partStyle={{
          borderWidth: '1px',
          borderColor: '#000'
        }}
      >
      {
        this.props.verticalScrollbarShow &&
        <VerticalScrollBar
          zIndex={1}
          value={parseInt(this.props.verticalScrollbarProgress)}/>
      }

      <TextParts {...this.props}/>
    </Border>);

  }

});

module.exports = TextArea;



