'use strict';
var React = require('react');
var Border = require('../common/Border');
var styleHelper = require('../../helper/styleHelper');
var AutoSizeHelper = require('../common/AutoSizeHelper');
var Dom = require('react-dom');
var LabelParts = require('../common/LabelParts');
var convertHelper = require('../../types/convertHelper');
var textHelper = require('../../helper/util').Text;

var Label = React.createClass({
  componentWillReceiveProps: function (nextProps) {
    //if (nextProps.text !== this.props.text) {
    this.isInvalid = true;
    //}
  },

  componentDidUpdate: function () {
    if (this.isInvalid) {
      this.isInvalid = false;
      this._doPaint();
    }
  },

  _doPaint: function () {
    var com;
    var parentDom;
    var size;

    if (this.props.isAutoSize) {
      com = Dom.findDOMNode(this);
      parentDom = com.parentElement;

      while (!parentDom && (parentDom.id !== this.props.controlID)) {
        parentDom = parentDom.parentElement;
      }

      if (parentDom) {
        size = AutoSizeHelper.getComAutoSize(this.props);
        parentDom.style.width = size.width + 4 + 'px';
        parentDom.style.height = size.height + 2 + 'px';
      }
    }
  },

  componentDidMount: function () {
    this._doPaint();
  },

  render: function () {
    var text = styleHelper.getText(this.props);
    var labelStyle;
    var color = styleHelper.getTextColor(this.props);
    var fontSize = styleHelper.getFontSize(this.props);
    var bgColor = styleHelper.getBackgroundColor(this.props);
    text = textHelper.convertSpaceToNbsp(text);

    labelStyle = {
      fontStyle: this.props.textStyles ? this.props.textStyles.fontStyle : 'normal',
      textDecoration: this.props.textStyles ? this.props.textStyles.textDecoration : 'normal',
      fontWeight: this.props.textStyles ? this.props.textStyles.fontWeight : 'normal',
      fontFamily: this.props.textFontFamily || '',
      color: color || '#000',
      fontSize: fontSize || 14,
      wordWrap: 'nowrap',
      pointerEvents: 'none',
      textAlign: this.props.textAlign ? convertHelper.getTextHorizontalAlign(this.props.textAlign) : 'left',
      whiteSpace: 'nowrap'
    };
    styleHelper.adjustTextSizeStyle(labelStyle);
    return (<div
      id={this.props.id}
      style={{
        width: this.props.isAutoSize ? 'auto' : styleHelper.getWidth(this.props),
        height: this.props.isAutoSize ? 'auto' : styleHelper.getHeight(this.props),
        position: 'absolute',
        overflow : this.props.isAutoSize ?'visible':'hidden',
        backgroundColor: bgColor
      }}
    >
      <label
        style={labelStyle}
      >{text}</label>
    </div>);
  }
});

module.exports = Label;



