'use strict';

var React = require('react');
var ReactDOM = require('react-dom');
var appTypes = require('../../types/appTypes.js');
var PopupHelper = require('../../interactive/PopupHelper');
var util = require('../../helper/util');

/*
 @Author : mengxiaojiang
 @CreateDate : 2016-4-1
 @Descript : 弹出面板组件
 */
var PopupPanel = React.createClass({
  render: function () {
    var bgComp = this.props.items[0];
    var contentComp = this.props.items[1];
    var bg = util.Color.setOpacity(bgComp.color,0.4);
    var dom = <div
      key={this.props.controlID}
      id={this.props.controlID}
      className='component component-PopupPanel popup-control'
      style={{
        visibility : this.props.popup?'visible': 'hidden',
        zIndex : this.props.zOrder
      }}>
      <div id={this.props.controlID + '-background'}
           className="background"
           style={{
             width : this.props.measureWidth || 0,
             height : this.props.measureHeight || 0,
             backgroundColor : this.props.isShowBackground?bg:0
           }}
           ref="background"
      ></div>
      <div id={this.props.controlID + '-content'}
           className="content"
           ref="content"
           style={{
             left : contentComp.x - 0.5,
             top : contentComp.y - 0.5,
             width : contentComp.w + 0.5,
             height : contentComp.h + 0.5,
             opacity : this.props.popup?1:0,
             boxSizing : 'border-box'
           }}>
        {
          this.props.children
        }
      </div>
    </div>;
    return (dom);
  },

  componentDidMount: function () {
    this.doPopup();
  },

  componentDidUpdate: function () {
    this.doPopup();
  },

  doPopup: function () {
    PopupHelper.registerPopup(this);
    if (this.open) {
      this.open();
    }
  }
});

module.exports = PopupPanel;



