'use strict';

var React = require('react');
var _ = require('underscore');
var appTypes = require('../../types/appTypes');
var TextParts = require('../common/TextParts');
var styleHelper = require('../../helper/styleHelper');

/**
 * 工具提示
 */
var ToolTip = React.createClass({

  _drawTopLine: function (graphics, x, y, width, height) {

    var middle = width / 2 + x;
    if (this.props.tipCornerDirection === appTypes.ToolTipDirection.LEFT_TOP) {
      graphics.lineTo(x + 18, y);
      graphics.lineTo(x + 14, y - this.angleHeight);
      graphics.lineTo(x + 27, y);
    } else if (this.props.tipCornerDirection === appTypes.ToolTipDirection.TOP) {
      graphics.lineTo(middle - 6, y);
      graphics.lineTo(middle, y - this.angleHeight);
      graphics.lineTo(middle + 6, y);
    } else if (this.props.tipCornerDirection === appTypes.ToolTipDirection.RIGHT_TOP) {
      graphics.lineTo(width + x - 27, y);
      graphics.lineTo(width + x - 14, y - this.angleHeight);
      graphics.lineTo(width + x - 18, y);
    }

    graphics.lineTo(width - this.radius + x, y);
  },

  _drawBottomLine: function (graphics, x, y, width, height) {
    var middle = width / 2 + x;

    if (this.props.tipCornerDirection === appTypes.ToolTipDirection.LEFT_BOTTOM) {
      graphics.lineTo(x + 27, height + y);
      graphics.lineTo(x + 14, height + y + this.angleHeight);
      graphics.lineTo(x + 18, height + y);
    } else if (this.props.tipCornerDirection === appTypes.ToolTipDirection.BOTTOM) {
      graphics.lineTo(middle + 6, height + y);
      graphics.lineTo(middle, height + y + this.angleHeight);
      graphics.lineTo(middle - 6, height + y);
    } else if (this.props.tipCornerDirection === appTypes.ToolTipDirection.RIGHT_BOTTOM) {
      graphics.lineTo(width + x - 18, height + y);
      graphics.lineTo(width + x - 14, height + y + this.angleHeight);
      graphics.lineTo(width + x - 27, height + y);
    }

    graphics.lineTo(x + this.radius, height + y);
  },

  _drawLeftLine: function (graphics, x, y, width, height) {

    var middle = height / 2 + y;

    if (this.props.tipCornerDirection === appTypes.ToolTipDirection.LEFT) {
      graphics.lineTo(x, middle + 6);
      graphics.lineTo(x - this.angleHeight, middle);
      graphics.lineTo(x, middle - 6);
    } else {
      graphics.lineTo(x, this.radius + y);
    }
  },

  _drawRightLine: function (graphics, x, y, width, height) {

    var middle = height / 2 + y;
    if (this.props.tipCornerDirection == appTypes.ToolTipDirection.RIGHT) {
      graphics.lineTo(width + x, middle - 6);
      graphics.lineTo(width + x + this.angleHeight, middle);
      graphics.lineTo(width + x, middle + 6);
    } else {
      graphics.lineTo(width + x, height + y - this.radius);
    }
  },

  _drawRadiusRect: function (graphics, x, y, width, height) {
    graphics.beginPath();
    y += 0.5;
    x += 0.5;
    height -= 0.5;
    width -= 0.5;
    graphics.arc(this.radius + x, this.radius + y, this.radius, Math.PI, Math.PI * 3 / 2);

    this._drawTopLine(graphics, x, y, width, height);

    graphics.arc(width - this.radius + x - 0.5, this.radius + y, this.radius, Math.PI * 3 / 2, Math.PI * 2);

    this._drawRightLine(graphics, x, y, width - 0.5, height);

    graphics.arc(width - this.radius + x - 0.5, height + y - this.radius - 0.5, this.radius, 0, Math.PI * 1 / 2);

    this._drawBottomLine(graphics, x, y, width, height- 0.5);

    graphics.arc(x + this.radius, height + y - this.radius, this.radius, Math.PI * 1 / 2, Math.PI);

    this._drawLeftLine(graphics, x, y, width, height);

    graphics.closePath();
  },

  _doPaint: function () {
    var selfDom = ReactDOM.findDOMNode(this.refs.canvas);
    var graphics = selfDom.getContext('2d');
    var clientWidth = selfDom.clientWidth;
    var clientHeight = selfDom.clientHeight;


    var direction = this.props.tipCornerDirection;
    var x = 0;
    var y = 0;
    var width = clientWidth;
    var height = clientHeight;


    if (direction === appTypes.ToolTipDirection.LEFT) {
      x = this.angleHeight;
      width = clientWidth - this.angleHeight;
    } else if (direction === appTypes.ToolTipDirection.BOTTOM || direction === appTypes.ToolTipDirection.LEFT_BOTTOM || direction === appTypes.ToolTipDirection.RIGHT_BOTTOM) {
      height = clientHeight - this.angleHeight;
    } else if (direction === appTypes.ToolTipDirection.TOP || direction === appTypes.ToolTipDirection.LEFT_TOP || direction === appTypes.ToolTipDirection.RIGHT_TOP) {
      y = this.angleHeight;
      height = clientHeight - this.angleHeight;
    } else if (direction === appTypes.ToolTipDirection.RIGHT) {
      width = clientWidth - this.angleHeight;
    }


    graphics.save();

    this.requestID = window.requestAnimationFrame(function () {

      graphics.clearRect(0, 0, styleHelper.getWidth(this.props), styleHelper.getHeight(this.props));
      graphics.strokeStyle = '#000';
      graphics.lineWidth = 1;
      graphics.strokeWidth = 1;
      graphics.fillStyle = styleHelper.getBackgroundColor(this.props);

      this._drawRadiusRect(graphics, x, y, width, height);
      graphics.fill();
      graphics.stroke();

    }.bind(this));
  },

  componentWillUnmount: function () {
    if (this.requestID) {
      cancelAnimationFrame(this.requestID);
    }
  },

  componentDidUpdate: function () {
    this._doPaint();
  },

  componentDidMount: function () {
    this._doPaint();
  },


  render: function () {

    this.radius = 4;
    this.angleHeight = 10;
    var padding = 4;

    var direction = this.props.tipCornerDirection;
    var x = padding;
    var y = padding;
    var w = styleHelper.getWidth(this.props);
    var h = styleHelper.getHeight(this.props);
    var width = w;
    var height = h;

    if (direction === appTypes.ToolTipDirection.LEFT) {
      x = this.angleHeight + padding;
      width = width - this.angleHeight - padding * 2;
      height = height - padding * 2;
    } else if (direction === appTypes.ToolTipDirection.BOTTOM || direction === appTypes.ToolTipDirection.LEFT_BOTTOM || direction === appTypes.ToolTipDirection.RIGHT_BOTTOM) {
      height = height - this.angleHeight - padding * 2;
      width = width - padding * 2;
    } else if (direction === appTypes.ToolTipDirection.TOP || direction === appTypes.ToolTipDirection.LEFT_TOP || direction === appTypes.ToolTipDirection.RIGHT_TOP) {
      y = this.angleHeight + padding;
      height = height - this.angleHeight - padding * 2;
      width = width - padding * 2;
    } else if (direction === appTypes.ToolTipDirection.RIGHT) {
      width = this.props.w - this.angleHeight - padding * 2;
      height = height - padding * 2;
    }

    var textStyle = {
      overflow: 'hidden',
      position: 'absolute',
      zIndex: 1
    };

    var props = _.extend({}, this.props);
    props.x = 0;
    props.y = 0;
    props.w = width;
    props.h = height;
    return (<div>
      <canvas ref="canvas"
              left={0}
              top={0}
              width={w}
              height={h}
      />
      <div style={{
        width:width,
        height:height,
        left:x,
        top:y,
        position:'absolute'
      }}>
        <TextParts {...props} partStyle={textStyle}/>
      </div>

    </div>);
  }

});

module.exports = ToolTip;



